header {
    background-color: #011627;
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    padding: 20px;
    position: relative;
    z-index: 1;
    height: clamp(6rem, 10vh, 10vh);
    min-width: calc(100vw);
    align-items: center;
    color: #FDFFFC;
    justify-content: space-between;
}
.logo {
    color: var(--light-color);
    font-weight: 900;
}

.logo:hover {
    color: var(--tertiary-color);
}
.dropdown {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    position: absolute;
    top: 110%;
    font-size: 1.5rem;
    padding: 0.5rem 2rem;
    background-color: var(--dark-color);
    border-radius: 2.5px;
    transition: -webkit-filter 0.3s ease-in-out;
    transition: filter 0.3s ease-in-out;
    transition: filter 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
    -webkit-filter: brightness(1.4);
            filter: brightness(1.4)
}

.dropdown-item:hover {
    color: var(--tertiary-color);
}


.dropdown-menu {
    font-size: clamp(1rem, 1.5vw, 1.5rem);
    padding: 0.5rem 2rem;
    background-color: var(--dark-color);
    border-radius: 5px;
    font-weight: 600;
    transition: -webkit-filter 0.3s ease-in-out;
    transition: filter 0.3s ease-in-out;
    transition: filter 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
    -webkit-filter: brightness(1.4);
            filter: brightness(1.4);
    width: 15rem;
    text-align: center;
}

.dropdown-menu:hover {
    -webkit-filter: brightness(1.7);
            filter: brightness(1.7)
}
.generic-button {
    font-size: clamp(1rem, 1.5vw, 1.5rem);
    padding: 0.5rem 2rem;
    background-color: var(--light-color);
    color: var(--dark-color);
    border-radius: 5px;
    font-weight: 600;
    transition: -webkit-filter 0.3s ease-in-out;
    transition: filter 0.3s ease-in-out;
    transition: filter 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
    -webkit-filter: brightness(0.8);
            filter: brightness(0.8);
    /* width: 15rem; */
    text-align: center;
}

.generic-button:hover {
    -webkit-filter: brightness(1.4);
            filter: brightness(1.4)
}
.start-button {
    padding: 0.5rem 2rem;
    background-color: #E71D36;
    border-radius: 5px;
    font-size: 2rem;
    font-weight: 600;
    transition: all 0.3s ease-in-out;
}

.start-button:hover {
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
}
main {
    width: 100vw;
    height: clamp(50rem, 90vh, 90vh);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 3rem;
    gap: 3rem;
}
.container {
    background-color: #011627;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 50%;
    height: 7.5vh;
    padding: 30px;
}
.clear-button {
    display: inline-block;
    position: relative;
    padding: 0.5rem 2rem;
    border-radius: 5px;
    background-color: #E71D36;
    color: #FDFFFC;
    cursor: pointer;
    font-weight: 600;
    transition: all 0.3s ease-in-out;
}

.clear-button:hover {
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
}
.grid-container {
    width: 90vw;
    height: calc((((90vw - 84px) / 41) * 15) + 32px);
    /* height: 75vh; */
    background-color: #011627;
    padding: 2px;
}
.grid-node {
    background: var(--light-color);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0;
    transition: background 100ms ease;
}

.grid-node > * {
    display: none;
}

.grid-node:hover {
    background: #FF9F1C;
}

.isStart, .isEnd {
    background: var(--tertiary-color);
}

.isWall {
    background: var(--primary-color);
} 

:not(.isStart, .isEnd).isSeen {
    -webkit-animation: search 1s ease-in-out 1;
            animation: search 1s ease-in-out 1;
    background: var(--secondary-color);
}

:not(.isStart, .isEnd).isPath {
    -webkit-animation: path 1s ease-in-out 1;
            animation: path 1s ease-in-out 1;
    background: var(--tertiary-color);
}

.grid-node > .isStart, .grid-node > .isEnd, .grid-node > .isWall {
    display: inline-block;
    font-size: clamp(.5rem, 1.3vw, 3rem);
}

@-webkit-keyframes path {
    0% {
        background: var(--light-color);
    }
    33% {
        background: var(--dark-color);
    }
    66% {
        background: var(--primary-color);
    }
    100% {
        background: var(--tertiary-color);
    }
}

@keyframes path {
    0% {
        background: var(--light-color);
    }
    33% {
        background: var(--dark-color);
    }
    66% {
        background: var(--primary-color);
    }
    100% {
        background: var(--tertiary-color);
    }
}

@-webkit-keyframes search {
    0% {
        background: var(--light-color);
    }
    50% {
        background: var(--tertiary-color);
    }
    100% {
        background: var(--secondary-color);
    }
}

@keyframes search {
    0% {
        background: var(--light-color);
    }
    50% {
        background: var(--tertiary-color);
    }
    100% {
        background: var(--secondary-color);
    }
}
.grid {
    --node-size: calc((90vw - 84px) / 41);

    display: grid;
    grid-template-columns: repeat(auto-fill, var(--node-size));
    grid-gap: 2px;
    grid-auto-rows: var(--node-size);
}
.instructions-container {
    width: 70%;
    height: 50rem;
    background-color: var(--light-color);
    border-radius: 2rem;
    border: 10px solid var(--dark-color);
    position: absolute;    
    left: calc(50% - (70% / 2));
    top: calc(50% - (45rem / 2));
    z-index: 10;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 2rem;
    padding: 3rem;
    grid-gap: 4rem;
    gap: 4rem;
}

.instructions-container-close {
    position: absolute;
    right: 30px;
    top: 15px;
    font-size: 3rem;
}

.arrow-left:hover, .arrow-right:hover {
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: all .5s ease;
}

.instructions-container-data {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

.language-buttons-container {
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    justify-content: center;
    align-items: center;
}

.language-button {
    padding: 1.5rem 2rem;
    background-color: var(--primary-color);
    border-radius: 1rem;
    color: var(--light-color);
    border: 5px solid var(--dark-color);
}

.language-button:hover {
    background-color: var(--dark-color);
    color: var(--light-color);
}
.window-warning {
    display: flex;
    flex-direction: column;
    grid-gap: 5rem;
    gap: 5rem;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100vh;
    background: var(--dark-color) ;
}

.error-title {
    font-size: 3rem;
    font-weight: bold;
    color: var(--primary-color);
    margin: 1rem 5vw;
}

.error-message {
    font-size: 1.5rem;
    font-weight: bold;
    color: var(--light-color);
    margin: 0 5vw;
}

.giphy-embed {
    width: 90%;
    border-radius: 50%;
}
.App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background-color: #FDFFFC;
}
:root {
  --dark-color: #011627;
  --light-color: #FDFFFC;
  --primary-color: #E71D36;
  --secondary-color: #2EC4B6;
  --tertiary-color: #FF9F1C;
  --text-color: var(var(--light-color));
  --animation-speed: 500ms;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

p {
  color: var(var(--light-color));
  color: var(--text-color);
  margin: 0;
}

button, input[type="submit"], input[type="reset"] {
	background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

